@import '~node_modules/modern-normalize/modern-normalize.css';

h1,
h2,
h3,
h4,
h5,
h6,
ul,
p {
    margin: 0;
}

a {
    text-decoration: none;
    outline: none;
}

ul {
    padding: 0;
    list-style-type: none;
}

input,
textarea,
select {
    padding: 0;
    outline: none;
}

button {
    margin: 0;
    padding: 0;
    border: 0;

    @include mq(desktop) {
        cursor: pointer;
    }
}