*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --spacing: 4px;
  --animation-duration: 250ms;
  --animation-timing-function: cubic-bezier(.4, 0, .2, 1);
  --display: block;
  --header-fix: 60px;
  --hero-height-img-parallax: calc(50vh - var(--header-fix) );
  --hero-height-main-img-parallax: calc(50vh - var(--header-fix) );
  --hero-height-video-parallax: calc(50vh - var(--header-fix) );
  --text-main: #ca9b4a;
  --text-second: #081617;
  --text-third: white;
  --text-logo: #ca9b4a;
  --text-shadow: #081617;
  --bgc-main: #02302d;
  --bgc-linear: #0f5e58;
  --bgc-second: #b9aa71;
  --bgc-third: #d1dfc9;
  --accent-color: #ebebeb;
  --think-line: rgba(202, 155, 74, .506);
  --loader-color-first: white;
  --loader-color-second: #ca9b4a;
  --arrow-color: #b9aa71;
  --box-shadow: white;
  --form-bgc-color-main: #7c5c5c;
  --form-text-color-main: #fff;
  --form-accent-main: #7a956b;
  --form-accent-shadow-color: #fff;
  --form-error-shadow-color: #ed4337;
  --form-file-load-color: #77608d;
  --form-bgc-color-checkbox: #fff;
  --form-sumbit-shadow: #161b13;
  --form-sumbit-hover: #4f6146;
  --scroll-track-shadow: rgba(0, 0, 0, .3);
  --scroll-thumb-shadow: rgba(0, 0, 0, .5);
}

@media screen and (min-width: 768px) {
  :root {
    --header-fix: 80px;
  }
}

@media screen and (min-width: 768px) {
  :root {
    --hero-height-main-img-parallax: calc(80vh - var(--header-fix) );
  }
}

@media screen and (min-width: 1140px) {
  :root {
    --hero-height-main-img-parallax: calc(100vh - var(--header-fix) );
  }
}

@media screen and (min-width: 768px) {
  :root {
    --hero-height-video-parallax: calc(80vh - var(--header-fix) );
  }
}

@media screen and (min-width: 1140px) {
  :root {
    --hero-height-video-parallax: calc(100vh - var(--header-fix) );
  }
}

h1, h2, h3, h4, h5, h6, ul, p {
  margin: 0;
}

a {
  outline: none;
  text-decoration: none;
}

ul {
  padding: 0;
  list-style-type: none;
}

input, textarea, select {
  outline: none;
  padding: 0;
}

button {
  border: 0;
  margin: 0;
  padding: 0;
}

@media screen and (min-width: 1140px) {
  button {
    cursor: pointer;
  }
}

