body {
  letter-spacing: .03px;
  background-color: var(--bgc-third);
  font-family: Roboto, sans-serif;
  font-size: 14px;
  line-height: 1.17;
}

@media screen and (max-width: 1139px) {
  body.modal-open {
    overflow: hidden;
  }
}

html, body {
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  padding-left: calc(5 * var(--spacing) );
  padding-right: calc(5 * var(--spacing) );
  margin: 0 auto;
}

@media screen and (min-width: 480px) {
  .container {
    width: 440px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 700px;
    padding-left: 34px;
    padding-right: 34px;
  }
}

@media screen and (min-width: 1140px) {
  .container {
    width: 1110px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.section {
  padding-top: calc(8 * var(--spacing) );
  padding-bottom: calc(8 * var(--spacing) );
}

@media screen and (min-width: 1140px) {
  .section {
    padding-top: calc(12 * var(--spacing) );
    padding-bottom: calc(12 * var(--spacing) );
  }
}

.hidden-anchor {
  top: calc(var(--hero-height-img-parallax)  + var(--header-fix) );
  visibility: hidden;
  display: block;
  position: relative;
}

.footer-fixed-wrapper {
  height: calc(100% + 1px);
  padding-top: var(--header-fix);
  flex-direction: column;
  display: flex;
  position: relative;
}

.page-content {
  flex-grow: 1;
  position: relative;
}

.page-content__parallax {
  z-index: 3;
  background-color: var(--bgc-third);
  position: relative;
  top: 0;
  left: 0;
}

.page-content__parallax--gradient {
  background: linear-gradient(4deg, #5462c3, #e0cba6);
}

.hero {
  min-height: var(--hero-height-img-parallax);
  flex-direction: column;
  display: flex;
  position: relative;
}

.hero--main {
  min-height: var(--hero-height-main-img-parallax);
}

.hero--video {
  min-height: var(--hero-height-video-parallax);
}

.hero__overlay {
  z-index: 2;
  padding: calc(8 * var(--spacing) ) calc(4 * var(--spacing) );
  background-color: rgba(0, 0, 0, .2);
  flex-direction: column;
  flex: auto;
  justify-content: flex-end;
  align-items: center;
  display: flex;
  position: relative;
}

.hero__title {
  color: var(--text-third);
  text-shadow: var(--text-shadow) 3px 3px;
  text-align: center;
  font-family: Gloria Hallelujah, sans-serif;
  font-size: 44px;
}

@media screen and (min-width: 768px) {
  .hero__title {
    font-size: 54px;
  }
}

@media screen and (min-width: 1140px) {
  .hero__title {
    font-size: 60px;
  }
}

.hero__background {
  top: var(--header-fix);
  width: 100%;
  height: var(--hero-height-img-parallax);
  object-fit: cover;
  position: fixed;
  left: 0;
}

.hero__background--main {
  height: var(--hero-height-main-img-parallax);
}

.hero__background--video {
  height: var(--hero-height-video-parallax);
}

.logo {
  max-width: 200px;
  letter-spacing: .05em;
  align-items: center;
  padding-top: 7px;
  padding-bottom: 7px;
  font-family: Roboto Slab, sans-serif;
  font-size: 20px;
  font-weight: 500;
  display: flex;
}

@media screen and (min-width: 768px) {
  .logo {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.logo__img--animate {
  animation: logo 1000ms var(--animation-timing-function);
}

@keyframes logo {
  0% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.logo__text {
  text-align: center;
  color: var(--text-logo);
  text-shadow: var(--text-shadow) 1px 1px;
}

.lang-menu {
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1139px) {
  .lang-menu {
    justify-content: center;
  }
}

@media screen and (max-width: 1139px) {
  .lang-menu {
    margin-top: calc(5 * var(--spacing) );
  }
}

.lang-menu__item {
  display: flex;
}

.lang-menu__item:not(:last-child) {
  margin-right: calc(1 * var(--spacing) );
}

.lang-menu__item:not(:last-child):after {
  content: "";
  width: 1px;
  height: 50%;
  margin-left: calc(1 * var(--spacing) );
  background-color: var(--bgc-second);
  display: block;
  transform: translateY(50%);
}

.lang-menu__btn {
  padding: calc(3 * var(--spacing) );
  text-transform: uppercase;
  color: var(--text-main);
  transition: background-color var(--animation-duration) var(--animation-timing-function), color var(--animation-duration) var(--animation-timing-function);
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px 10px;
  font-size: 20px;
}

.lang-menu__btn:hover {
  color: var(--accent-color);
}

@media screen and (min-width: 1140px) {
  .lang-menu__btn {
    border-radius: 10px 5px;
  }
}

.socials {
  padding-top: calc(3 * var(--spacing) );
  padding-bottom: calc(3 * var(--spacing) );
  justify-content: center;
  align-items: center;
  display: flex;
}

.socials__item {
  width: 44px;
  height: 44px;
}

.socials__item:not(:first-child) {
  margin-left: calc(5 * var(--spacing) );
}

.socials__link {
  width: 100%;
  height: 100%;
  color: var(--text-second);
  background-color: var(--bgc-third);
  transition: color var(--animation-duration) var(--animation-timing-function), background-color var(--animation-duration) var(--animation-timing-function);
  border: none;
  border-radius: 20px 10px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.socials__link:hover, .socials__link:focus {
  background-color: var(--bgc-second);
}

.socials__icon {
  fill: currentColor;
}

.button {
  letter-spacing: .05em;
  color: var(--form-text-color-main);
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 700;
  display: flex;
  position: relative;
  top: 0;
}

.button--box-shadow:focus {
  box-shadow: 1px 1px 15px var(--accent-color);
}

.button:focus {
  outline: none;
}

.button:hover {
  background-color: var(--form-sumbit-hover);
}

.button:active {
  box-shadow: 1px 2px 0 var(--form-sumbit-shadow);
  top: 3px;
}

.btn-top {
  z-index: 4;
  padding-top: calc(2 * var(--spacing) );
  padding-bottom: calc(3 * var(--spacing) );
  padding-right: calc(3 * var(--spacing) );
  padding-left: calc(4 * var(--spacing) );
  color: var(--text-second);
  background-color: var(--bgc-second);
  transition: background-color var(--animation-duration) var(--animation-timing-function), color var(--animation-duration) var(--animation-timing-function);
  border-top-left-radius: 80%;
  outline: none;
  flex-direction: column;
  align-items: center;
  font-family: Gloria Hallelujah, sans-serif;
  font-size: 16px;
  display: none;
  position: fixed;
  bottom: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .btn-top {
    padding-top: calc(3 * var(--spacing) );
    padding-bottom: calc(3 * var(--spacing) );
    padding-right: calc(5 * var(--spacing) );
    padding-left: calc(5 * var(--spacing) );
    font-size: 18px;
  }
}

@media screen and (min-width: 1140px) {
  .btn-top:hover {
    background-color: var(--bgc-third);
  }
}

.btn-top__icon {
  width: 30px;
  fill: currentColor;
}

.loader {
  z-index: 100;
  width: 40px;
  height: 40px;
  border-top: 4px solid var(--loader-color-first);
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: none;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

@media screen and (max-width: 767px) {
  .loader {
    width: 30px;
    height: 30px;
    bottom: 10px;
    right: 10px;
  }
}

.loader:after {
  content: "";
  width: 40px;
  height: 40px;
  border-left: 4px solid var(--loader-color-second);
  border-bottom: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (max-width: 767px) {
  .loader:after {
    width: 30px;
    height: 30px;
  }
}

.loader--is-active {
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

::-webkit-scrollbar {
  width: 1vw;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px var(--scroll-track-shadow);
  border-radius: 10px 0 0 10px;
}

::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px var(--scroll-thumb-shadow);
  border-radius: 10px 0 0 10px;
}

.color-theme {
  width: 46px;
  height: 46px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

@media screen and (max-width: 1139px) {
  .color-theme {
    margin-top: calc(5 * var(--spacing) );
  }
}

@media screen and (min-width: 768px) and (max-width: 1139px) {
  .color-theme {
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 1140px) {
  .color-theme {
    margin-left: calc(3 * var(--spacing) );
  }
}

.sun {
  --s: 3px;
  --o: 3px;
  --c: var(--bgc-second);
  --w: 3px;
  --grad: transparent calc(50% - var(--w) / 2), var(--c) calc(50% - var(--w) / 2 + 1px) calc(50% + var(--w) / 2 - 1px), transparent calc(50% + var(--w) / 2);
  width: 12px;
  height: 12px;
  margin: calc(var(--s)  + var(--o)  + 10px);
  background-color: var(--bgc-second);
  pointer-events: none;
  opacity: 0;
  border-radius: 50%;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  position: relative;
  transform: scale(.6)rotate(0deg);
}

.sun:before {
  content: "";
  top: calc(-1 * var(--s)  - var(--o) );
  bottom: calc(-1 * var(--s)  - var(--o) );
  left: calc(-1 * var(--s)  - var(--o) );
  right: calc(-1 * var(--s)  - var(--o) );
  border-radius: inherit;
