.menu {
    width: 280px;
    
    @include mq(notDesktop) {
        position: fixed;
        top: 0;
        right: 0;
        z-index: 5;

        height: 100%;

        background: linear-gradient(1deg, var(--bgc-main) 95%, var(--bgc-linear));
        border-bottom-left-radius: 50px;
        box-shadow: -3px 3px 10px var(--box-shadow);

        pointer-events: none;
        transform: translateX(120%);
        transition: transform var(--animation-duration) var(--animation-timing-function);
    }

    @include mq(tablet) {
        width: 500px;
    }

    @include mq(desktop) {
        width: 100%;
    }

    &.is-open {
        pointer-events: auto;
        transform: translateX(0);

        @media screen and (max-height: 700px) {
            overflow: scroll;
            border-bottom-left-radius: 0px;
        }
    }
}

.menu__container {
    @include mq(notDesktop) {
        padding: calc(15 * var(--spacing)) calc(5 * var(--spacing));
    }

    @include mq(tablet-only) {
        text-align: center;
    }

    @include mq(desktop) {
        display: flex;
        justify-content: end;
        align-items: center;
    }
}

.menu__mob-btn-open {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;

    background-color: transparent;

    @include mq(desktop) {
        display: none;
    }
}

.menu__mob-icon-open {
    fill: var(--bgc-second);
    transition: fill var(--animation-duration) var(--animation-timing-function);

    &:hover,
    &:focus {
        fill: var(--accent-color);
    }
}

.menu__mob-btn-close {
    position: fixed;
    top: 10px;
    right: 10px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: transparent;
    
    @include mq(desktop) {
        display: none;
    }
}

.menu__mob-icon-close {
    fill: var(--bgc-second);
    transition: fill var(--animation-duration) var(--animation-timing-function);

    &:hover,
    &:focus {
        fill: var(--accent-color);
    }
}

.menu__label {
    position: fixed;
    top: 20px;
    left: 30px;

    font-family: Gloria Hallelujah, sans-serif;
    font-size: 22px;
    text-decoration: underline;
    color: var(--text-main);

    @include mq(desktop) {
        display: none;
    }
}